import type { ZhiFactory } from '@ali/4ever-cangjie';
import { moFactory } from '../mo/index';
import { createRenderNode } from './handlers/createRenderNode';
import queries from './queries';
import getNodeStyle from '../common/handlers/getNodeStyle';
import onAction from '../utils/handlers/onAction';
import shouldNodeComponentUpdate from '../common/handlers/shouldNodeComponentUpdate';
import createOnCangjieCopy from './handlers/createOnCangjieCopy';

export const createZhiFactory: (isMobile?: boolean) => ZhiFactory = (isMobile) => {
  return (configs) => {
    const tableConfig = configs.table || {};

    return {
      ...moFactory({}),
      queries,
      renderNode: createRenderNode(tableConfig, isMobile),
      getNodeStyle,
      onAction,
      shouldNodeComponentUpdate,
      onCangjieCopy: createOnCangjieCopy(isMobile),
    };
  };
};

export const zhiFactory = createZhiFactory();

export const mobileZhiFactory = createZhiFactory(true);
